const styles = {
  wrapper: {
    position: "relative",
    width: "100%",
    height: "60vh",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "flex-end",
    "&::before": {
      content: '""',
      position: "absolute",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      backgroundImage: `url(data:image/svg+xml;base64,)`,
      backgroundSize: "cover",
      backgroundPosition: "center",
      opacity: 0.8,
      zIndex: 1,
    },
    zIndex: 2,
  },
  container: {
    width: "100%",
    maxWidth: "1200px",
    margin: "0 auto",
    position: "relative",
    zIndex: 2,
  },
  logoWrapper: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  logo: {
    transition: "0.3s",
    width: "150px",
    height: "70px",
    margin: "0px 20px",
  },
  contactStack: {
    margin: "1rem 3rem",
    gap: "1rem",
    justifyContent: "center",
    alignItems: "flex-start",
  },
  contactItem: {
    display: "flex",
    alignItems: "center",
    gap: "0.5rem",
  },
  contactText: {
    color: "white",
    fontSize: { xs: "16px", sm: "18px", md: "20px" },
  },
};

export default styles;
